import React from 'react';
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import './App.css';
import styles from './assets/custom-styling.css';
import GoogleFontLoader from 'react-google-font-loader';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faChevronDown } from '@fortawesome/free-solid-svg-icons';
import { Container, Grid, Typography, Hidden, Paper } from "@material-ui/core";
import Emoji from "./components/emoji.component";
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grow from '@material-ui/core/Grow';
import Popper from '@material-ui/core/Popper';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import Home from './components/home.component';
import Projects from './components/projects.component';


function App() {


	const PAGES = {
		home: 'home',
		projects: 'projects',
		contacts: 'contacts',
		blogs: 'blogs'
	};

	const pageStyles = {
		home: {
			backgroundColor: "#030e18",
			foregroundColor: "#a6d5fa",
			accentColor: "#2196f3",
			navigatorButton: <Emoji symbol="🙋‍♂️" label="Hi!" />
		},
		projects: {
			backgroundColor: "#180605",
			foregroundColor: "#fab3ae",
			accentColor: "#f44336",
			navigatorButton: <Emoji symbol="💻" label="Try!" />
		},
		contacts: {
			backgroundColor: "#071107",
			foregroundColor: "#b7dfb9",
			accentColor: "#4caf50",
			navigatorButton: <Emoji symbol="📮" label="Ping!" />
		},
		blogs: {
			backgroundColor: "#190f00",
			foregroundColor: "#ffd599",
			accentColor: "#ff9800",
			navigatorButton: <Emoji symbol="📝" label="Read!" />
		},

	}

	const [pageStyle, setPageStyle] = React.useState(pageStyles.home)
	const [page, setPage] = React.useState(PAGES.home);

	function changePage(toPage) {
		if (toPage === 'home') {
			setPage(PAGES.home);
			setPageStyle(pageStyles.home);
		} else if (toPage === 'projects') {
			setPage(PAGES.projects);
			setPageStyle(pageStyles.projects);
		} else if (toPage === 'blogs') {
			setPage(PAGES.blogs);
			setPageStyle(pageStyles.blogs);
		} else if (toPage === 'contacts') {
			setPage(PAGES.contacts);
			setPageStyle(pageStyles.contacts);
		}
	}

	const [open, setOpen] = React.useState(false);
	const anchorRef = React.useRef(null);

	const handleToggle = () => {
		setOpen((prevOpen) => !prevOpen);
	};

	const handleClose = (event) => {
		if (anchorRef.current && anchorRef.current.contains(event.target)) {
			return;
		}

		setOpen(false);
	};

	function handleListKeyDown(event) {
		if (event.key === 'Tab') {
			event.preventDefault();
			setOpen(false);
		}
	}

	// return focus to the button when we transitioned from !open -> open
	const prevOpen = React.useRef(open);
	React.useEffect(() => {
		if (prevOpen.current === true && open === false) {
			anchorRef.current.focus();
		}

		prevOpen.current = open;
	}, [open]);


	let theme = createMuiTheme({
		palette: {
			type: 'dark',
			background: {
				default: "#1C1C1E"
			}
		},
	});

	return (
		<ThemeProvider theme={theme}>
			<CssBaseline />
			<GoogleFontLoader
				fonts={[
					{
						font: 'Montserrat',
						weights: [100, 200, 300, 400, 500, 600, 700, 800, 900],
					},
					{
						font: 'Raleway',
						weights: [100, 200, 300, 400, 500, 600, 700, 800, 900],
					},
					{
						font: 'Work Sans',
						weights: [0, 100, 0, 200, 0, 300, 0, 400, 0, 500, 0, 531, 0, 600, 0, 700, 0, 800, 1, 531],
					},
					{
						font: 'DM Sans',
						weights: [400, 500, 600, 700, 800, 900],
					},
					{
						font: 'Poppins',
						weights: [100, 200, 300, 400, 500, 600, 700, 800, 900],
					}
				]}
			/>
			<Container maxWidth="false"  >
				<Grid container direction="row" alignItems="center" justify="center" style={{ height: "85vh", marginTop: "7.5vh", marginBottom: "7.5vh" }}>
					<Hidden smDown>
						<Grid item md={2} lg={2} xl={2}></Grid>
					</Hidden>
					<Grid item xs={12} sm={12} md={8} lg={8} xl={8}>
						<Grid container direction="row" alignItems="center" >
							<Grid container direction="row" alignItems="center" >
								<Paper
									ref={anchorRef}
									aria-controls={open ? 'menu-list-grow' : undefined}
									aria-haspopup="true"
									onClick={handleToggle}
									style={{ paddingTop: '7px', paddingBottom: '7px', paddingLeft: "20px", paddingRight: "20px", backgroundColor: pageStyle.backgroundColor, color: pageStyle.foregroundColor, borderRadius: "20px" }}
									elevation={3}
								>
									<Grid container direction="row" alignItems="center">
										{
											pageStyle.navigatorButton
										}
										<Typography variant="subtitle1" style={{ marginLeft: '5px', fontFamily: 'Work Sans' }} >tanmoysg  <span style={{ color: pageStyle.accentColor, fontWeight: "800" }}> / </span>  {page === PAGES.home ? 'hello, world!' : page}</Typography>
									</Grid>
								</Paper>
								<Popper open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal style={{zIndex: '100'}}>
									{({ TransitionProps, placement }) => (
										<Grow
											{...TransitionProps}
											style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
										>
											<Paper elevation={3} style={{ backgroundColor: pageStyle.backgroundColor, color: pageStyle.foregroundColor, borderRadius: "7px", marginTop: '5px' }}>
												<ClickAwayListener onClickAway={handleClose}>
													<MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
														<MenuItem onClick={() => changePage(PAGES.home)}><Emoji symbol="🙋‍♂️" label="Hi!" /><Typography variant="subtitle1" style={{ marginLeft: '5px' }} >Home</Typography></MenuItem>
														<MenuItem onClick={() => changePage(PAGES.blogs)}><Emoji symbol="📝" label="Read!" /><Typography variant="subtitle1" style={{ marginLeft: '5px' }} >Blogs</Typography></MenuItem>
														<MenuItem onClick={() => changePage(PAGES.projects)}><Emoji symbol="💻" label="Try!" /><Typography variant="subtitle1" style={{ marginLeft: '5px' }} >Projects</Typography></MenuItem>
														<MenuItem onClick={() => changePage(PAGES.contacts)}><Emoji symbol="📮" label="Ping!" /><Typography variant="subtitle1" style={{ marginLeft: '5px' }} >Contact Me</Typography></MenuItem>
													</MenuList>
												</ClickAwayListener>
											</Paper>
										</Grow>
									)}
								</Popper>
							</Grid>
							<Grid container style={{ marginTop: "20px" }}>
								{
									page === PAGES.home ? <Home pagestyle={pageStyle} /> :
										page === PAGES.projects ? <Projects pagestyle={pageStyle} /> :
											page === PAGES.blogs ? <Home pagestyle={pageStyle} /> :
												page === PAGES.contacts ? <Home pagestyle={pageStyle} /> : <Home pagestyle={pageStyle} />
								}
							</Grid>
						</Grid>
					</Grid>
					<Hidden smDown>
						<Grid item md={2} lg={2} xl={2}></Grid>
					</Hidden>
				</Grid>
			</Container>
		</ThemeProvider>
	);

}

export default App;